<script>
import Page from "@/components/Page";
import Speedometer from "@/components/Speedometer";
import GraphPizza from "@/components/GraphPizza";
import GraphDoughnut from "@/components/GraphDoughnut";
import TableCustom from "@/components/TableCustom";
import GraphLine from "@/components/GraphLine";
import moment from "moment";
import _ from "lodash";
import ConnectionState from "@/components/ConnectionState.vue";
import Online from "@/components/status/Online.vue";
import Offline from "@/components/status/Offline.vue";
import Paused from "@/components/status/Paused.vue";
import PauseExceeded from "@/components/status/PauseExceeded.vue";
import { socket, state } from "@/socket";
import axios from "axios";

export default {
  name: "home",
  components: {
    Page,
    GraphPizza,
    GraphDoughnut,
    TableCustom,
    GraphLine,
    Speedometer,
    ConnectionState,
    Online,
    Offline,
    Paused,
    PauseExceeded,
  },
  metaInfo: {
    title: "Dashboard",
  },
  data: () => {
    return {
      tableModalData: [],
      ura: 0,
      queue: 0,
      serviceAbandon: 0,
      serviceAbandon20: 0,
      serviceComplete: 0,
      serviceComplete20: 0,
      serviceTimeComplete: 0,
      serviceTimeQueueComplete: 0,
      serviceTimeAbandon: 0,
      serviceTimeQueue: 0,
      callerOptions: {
        datasets: [
          {
            data: [0, 0],
          },
        ],
      },
      peakOptions: {
        datasets: [
          {
            data: [0, 1, 3, 5],
          },
        ],
      },
      allAgentsSocket: {},
      agentsOptions: {
        datasets: [
          {
            data: [0, 0, 0, 0, 0],
          },
        ],
      },
      agentOnlineSocket: 0,
      statusAgents: {
        online: 0,
        offline: 0,
        paused: 0,
        pauseExceeded: 0,
      },
      users: 0,
      totalCompletedCallsGraf: 0,
      totalAbandonedCallsGraf: 0,
      table: [],
      tableModal: [],
      uraInterval: null,
      abandonInterval: null,
      callersInterval: null,
      usersOnlineInterval: null,
      serviceTimeInterval: null,
      painelInterval: null,
      peakInterval: null,
      send: [],
      totalCallers: 0,
      gnsTitle: 0,
      tmrTitle: 0,
      tmfTitle: 0,
      txaTitle: 0,
      txaValueData: 0,
      tmfValueData: 0,
      tmrValueData: 0,
      gnsValueData: 0,
      dataVelocity: [],
    };
  },
  computed: {
    MetricsVelocity() {
      const Metrics = state.Metrics;
      console.log("Metrics", Metrics);
      if (Metrics && Metrics.has("0")) {
        const metric = Metrics.get("0");
        this.computeMetricsValues(metric);
        console.log("velocity metric", Metrics);
      }
      return Metrics;
    },

    AbandonedCallsAgents() {
      return state.abandonedCalls || new Map();
    },
    tableModalFields() {
      if (!this.tableModal[0]) return [];
      return Object.keys(this.tableModal[0]).map((key) => ({
        key,
        sortable: this.isSortable,
      }));
    },
    eventsws() {
      const allAgents = Object.fromEntries(state.QueueMemberAgent);
      this.updateEventsws(allAgents);
      return allAgents;
    },
    CallMetrics() {
      const customMetrics = state.CustomMetrics;
      this.customCallMetrics(customMetrics);
      return customMetrics;
    },

    PausedAgents() {
      const allAgentsExceededLimit =
        Object.fromEntries(state.ExceededLimit) || {};
      this.updateEventswsPauseExceeded(allAgentsExceededLimit);
      return allAgentsExceededLimit;
    },
  },
  methods: {
    async loadVelocity() {
      try {
        let origin = origin.replace(/^http(s)?:\/\//, "wss://");
        const hasPort = origin.match(/:\d+$/);

        if (hasPort) {
          origin = origin.replace(/:\d+$/, ":3333");
        } else {
          origin += ":3333";
        }
        const response = await axios.get(`${origin}/dashboard-velocity`);
        if (response && response.data && Array.isArray(response.data)) {
          this.dataVelocity = response.data;
        } else {
          console.error("Invalid response format:", response);
        }
      } catch (error) {
        console.error("Error loading velocity data:", error);
      }
    },

    async loadCallDuration() {
      try {
        let origin = origin.replace(/^http(s)?:\/\//, "wss://");
        const hasPort = origin.match(/:\d+$/);

        if (hasPort) {
          origin = origin.replace(/:\d+$/, ":3333");
        } else {
          origin += ":3333";
        }
        const response = await axios.get(`${origin}/call-duration-config`);
        this.dataCallDuration = response.data;
      } catch (error) {
        console.error("Erro ao carregar dados:", error);
      }
    },
    getCallDurationValue(limitForType) {
      if (this.dataCallDuration) {
        const item = this.dataCallDuration.find(
          (item) => item.limitForType === limitForType
        );
        return item ? item.limit : null;
      } else {
        return null;
      }
    },

    getSpeedometerValue(title) {
      const item = this.dataVelocity.find((item) => item.title === title);
      return item.value;
    },

    updateEventswsPauseExceeded(map) {
      this.statusAgents.pauseExceeded = Object.entries(map).filter(
        ([_, agent]) => agent.status === "Paused"
      ).length;
    },

    updateEventsws(allAgents) {
      this.statusAgents.online = Object.entries(allAgents).filter(
        ([_, agent]) => agent.status === "Online"
      ).length;

      this.statusAgents.offline = Object.entries(allAgents).filter(
        ([_, agent]) => agent.status === "Offline"
      ).length;

      this.statusAgents.paused = Object.entries(allAgents).filter(
        ([_, agent]) => agent.status === "Paused"
      ).length;
      this.allAgentsSocket = [
        Object.entries(allAgents).filter(
          ([_, agent]) => agent.reason === "Disponível"
        ).length,
        Object.entries(allAgents).filter(
          ([_, agent]) => agent.status === "Paused"
        ).length,
        Object.entries(allAgents).filter(
          ([_, agent]) => agent.reason === "Chamando"
        ).length,
        Object.entries(allAgents).filter(
          ([_, agent]) => agent.reason === "Ocupado"
        ).length,
        // Object.entries(allAgents)
        //   .filter(([_, agent]) => agent.reason === 'Pós-Atend')
        //   .length
      ];

      this.agentOnlineSocket =
        this.statusAgents.paused + this.statusAgents.online;

      let totalServiceTimeCallAnswering = 0;
      let totalServiceTimeWaitCancel = 0;
      let totalServiceTimeWaitAnswering = 0;
      let totalCompletedCalls = 0;
      let totalAbandonedCalls = 0;
      let postCallCount = 0;
      for (const [, agent] of Object.entries(allAgents).filter(
        ([_, agent]) => agent.status !== null
      )) {
        totalServiceTimeCallAnswering += agent.tempoAtendimento;
        totalServiceTimeWaitCancel += agent.tempoEsperaAbandono;
        totalServiceTimeWaitAnswering += agent.tempoEsperaAtendidas;
        postCallCount += agent.contagemPosChamada;
        totalCompletedCalls += agent.atendidas;
        totalAbandonedCalls += agent.abandonadas;
      }
      const averageServiceTimeCancel =
        totalServiceTimeWaitCancel / totalAbandonedCalls;
      const averageServiceTimeAnswering =
        totalServiceTimeCallAnswering / totalCompletedCalls;
      const averageServiceTimeWaitAnswering =
        totalServiceTimeWaitAnswering / postCallCount;
      const timeWait =
        (totalServiceTimeWaitAnswering + totalServiceTimeWaitCancel) /
        (postCallCount + totalAbandonedCalls);

      this.serviceTimeComplete =
        moment()
          .startOf("day")
          .seconds(averageServiceTimeAnswering)
          .format("HH:mm:ss") ?? "00:00:00";
      this.serviceTimeAbandon =
        moment()
          .startOf("day")
          .seconds(averageServiceTimeCancel)
          .format("HH:mm:ss") ?? "00:00:00";
      this.serviceTimeQueueComplete =
        moment()
          .startOf("day")
          .seconds(averageServiceTimeWaitAnswering)
          .format("HH:mm:ss") ?? "00:00:00";
      this.serviceTimeQueue =
        moment().startOf("day").seconds(timeWait).format("HH:mm:ss") ??
        "00:00:00";
      this.callerOptions = {
        datasets: [
          {
            data: [totalCompletedCalls ?? 0, totalAbandonedCalls ?? 0],
          },
        ],
      };
      this.totalCompletedCallsGraf = totalCompletedCalls;
      this.totalAbandonedCallsGraf = totalAbandonedCalls;
      this.totalCallers =
        (Number(totalCompletedCalls) || 0) + (Number(totalAbandonedCalls) || 0);
      this.getAbandonedCallValues();
    },
    getAbandonedCallValues() {
      const abandonedCallsAgents = this.AbandonedCallsAgents;
      const tableModalData = [];
      abandonedCallsAgents.forEach((value, key) => {
        const table = {
          ramal: value.ramal,
          nome: value.name,
          duração: moment()
            .startOf("day")
            .seconds(value.duration)
            .format("HH:mm:ss"),
          telefone: value.ultimo_telefone,
          Inicio: moment(value.startTime).format("HH:mm:ss"),
        };
        tableModalData.push(table);
      });
      this.tableModal = tableModalData;
    },
    abrirModalOnline() {
      this.$refs.modalOnline.showModal = true;
    },
    abrirModalOffline() {
      this.$refs.modalOffline.showModal = true;
    },
    abrirModalPaused() {
      this.$refs.modalPaused.showModal = true;
    },
    abrirModalPauseExceeded() {
      this.$refs.modalPauseExceeded.showModal = true;
    },
    computeMetricsValues(metric) {
      // Aqui você pode calcular os valores das métricas conforme necessário
      const txa = parseFloat(metric.txa.toFixed(2));
      const tmf = parseInt(metric.tmf);
      const tmr = parseInt(metric.tmr);
      const gns = parseInt(metric.gns);

      // Agora você pode usar esses valores como quiser, por exemplo, atribuí-los a variáveis do componente:
      this.txaTitle = txa;
      this.txaValueData = txa;
      this.tmfTitle = tmf;
      this.tmfValueData = parseFloat(metric.tmf).toFixed(2);
      this.tmrTitle = tmr;
      this.tmrValueData = parseFloat(metric.tmr).toFixed(2);
      this.gnsTitle = gns;
      this.gnsValueData = parseFloat(metric.gns).toFixed(2);
    },

    customCallMetrics(data) {
      let metric;
      data.forEach((value, key) => {
        if (key === "0") {
          metric = value;
        }
      });

      this.serviceAbandon = metric && metric.abandono ? metric.abandono : 0;
      this.serviceAbandon20 =
        metric && metric.abandono_x ? metric.abandono_x : 0;
      this.serviceComplete = metric && metric.complete ? metric.complete : 0;
      this.serviceComplete20 =
        metric && metric.complete_x ? metric.complete_x : 0;
    },
    getTime(seconds) {
      return moment().startOf("day").seconds(seconds).format("HH:mm:ss");
    },

    peakData(data) {
      if (!data || data.length === 0) {
        return [];
      }
      let time10 = 0;
      let time20 = 0;
      let time30 = 0;
      let time40 = 0;
      let time50 = 0;
      let time60 = 0;
      data.forEach((x) => {
        const val = parseInt(x.time);
        switch (true) {
          case _.inRange(val, 0, 10):
            time10++;
            break;
          case _.inRange(val, 10, 20):
            time20++;
            break;
          case _.inRange(val, 20, 30):
            time30++;
            break;
          case _.inRange(val, 30, 40):
            time40++;
            break;
          case _.inRange(val, 40, 50):
            time50++;
            break;
          case _.inRange(val, 50, 60):
            time60++;
            break;
        }
      });
      return [0, time10, time20, time30, time40, time50, time60];
    },
    modalChamadas(event) {
      if (typeof event === "string" && event.search("Abandonadas") === 0) {
        this.$refs.slideModal.show();
      } else if (event.abandonadas > 0) {
        this.$refs.slideModal.show();
      }
    },
  },
  mounted() {
    this.$store.commit("setTitle", "DashBoard");
    this.loadVelocity();
    this.callerOptions = {
      datasets: [
        {
          data: [
            this.totalCompletedCallsGraf ?? 0,
            this.totalAbandonedCallsGraf ?? 0,
          ],
        },
      ],
    };

    socket.on("SyncUraQueues", (...args) => {
      const arrayData = JSON.parse(args);
      if (arrayData.length > 0) {
        const result = arrayData[0];
        this.queue = result.fila;
        this.ura = result.ura;
      }
    });

    socket.on("SyncPeaks", (...args) => {
      const arrayData = JSON.parse(args);
      this.peakOptions = {
        datasets: [
          {
            data: this.peakData(arrayData),
          },
        ],
      };
    });
  },
  created() {
    this.allAgents = {};
    this.loadCallDuration();
    this.getAbandonedCallValues();
    this.iniciar();
  },
  beforeDestroy() {
    clearTimeout(this.idTimeout);
  },
  watch: {
    eventsws: {
      handler(newVal, oldVal) {},
      deep: true,
    },
    PausedAgents: {
      handler(newVal, oldVal) {},
      deep: true,
    },
    eventReport: {
      handler(newVal, oldVal) {},
      deep: true,
    },
    AbandonedCallsAgents: {
      handler(newVal, oldVal) {},
      deep: true,
    },
    MetricsVelocity: {
      handler(newVal, oldVal) {},
      deep: true,
    },
    CallMetrics: {
      handler(newVal, oldVal) {},
      deep: true,
    },
  },
};
</script>

