<template>
  <b-modal v-model="showModal" title="Agentes Online">
    <div class="card-content mt-1">
      <div class="table-responsive">
        <table id="tableagentTitle" class="table table-hover table-xl mb-0">
          <thead>
            <tr>
              <th class="border-top-0 text-center">
                Nome
              </th>
              <th class="border-top-0 text-center">Status</th>
              <th class="border-top-0 text-center">Tempo</th>
            </tr>
          </thead>
          <tbody id="filaAgents" v-if="Object.keys(onlineAgents).length > 0">
            <tr v-for="(status, extension) in onlineAgents" :key="extension">
              <td class="text-truncate text-center" id="posicao">

                <strong><span :class="colorName(status.reason)"></span> {{ status.name }}</strong>
              </td>
              <td class="text-truncate text-center p-1">
                <span :class="color(status.reason)">{{
                  status.reason
                }}</span>
              </td>

              <td class="text-center">
                <button type="button" class="btn btn-sm round" :class="colorClass(status.reason)">
                  <strong>{{ formatoSegundos(status.formattedElapsedTime) }}</strong>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </b-modal>
</template>

<script>
import { state } from '@/socket'

let allAgentsData = []

export default {
  name: 'Online',
  data () {
    return {
      showModal: false,
      agents: {},
      idTimeout: null
    }
  },
  computed: {
    onlineAgents () {
      const allAgents = Object.fromEntries(state.QueueMemberAgent)
      allAgentsData = allAgents
      return Object.entries(allAgents)
        .filter(([_, agent]) => agent.status === 'Online' || agent.status === 'Ocupado')
        .reduce((obj, [key, value]) => {
          if (!this.agents[key]) {
            this.$set(this.agents, key, { ...value })
          }
          obj[key] = this.agents[key]
          return obj
        }, {})
    }
  },
  methods: {
    color (status) {
      if (status === 'Disponível') {
        return 'text-success'
      } else if (status === 'Ocupado') {
        return 'text-danger'
      } else if (status === 'Chamando') {
        return 'text-muted'
      } else return 'text-warning'
    },
    colorName (status) {
      if (status === 'Disponível') {
        return 'ion ion-md-person text-success'
      } else if (status === 'Ocupado') {
        return 'ion ion-md-person text-danger'
      } else if (status === 'Chamando') {
        return 'ion ion-md-person text-muted'
      } else return 'ion ion-md-person text-warning'
    },
    colorClass (status) {
      if (status === 'Disponível') {
        return 'btn-outline-success'
      } else if (status === 'Ocupado') {
        return 'btn-outline-danger'
      } else if (status === 'Chamando') {
        return 'btn-outline-muted'
      } else {
        return 'btn-outline-warning'
      }
    },
    fecharModal () {
      this.showModal = false
    },
    agentsData () {
      this.$emit('agentsData', allAgentsData)
    },
    formatoSegundos (s) {
      const hours = Math.floor(s / 3600)
      const minutes = Math.floor((s % 3600) / 60)
      const seconds = Math.floor(s % 60)
      return `${this.zeroIzq(hours)}:${this.zeroIzq(minutes)}:${this.zeroIzq(seconds)}`
    },
    zeroIzq (n) {
      return n.toString().padStart(2, '0')
    },
    iniciar () {
      clearTimeout(this.idTimeout)
      Object.keys(this.agents).forEach(memberName => {
        this.agents[memberName].startTime = Date.now()
      })
      this.actualizar()
    },
    reiniciarCronometroParaAgente (memberName) {
      this.$delete(this.agents, memberName)
    },
    actualizar () {
      const agenteNoEstado = Object.fromEntries(state.QueueMemberAgent) || {}

      Object.keys(this.agents).forEach(memberName => {
        const agent = this.agents[memberName]
        const agentStatus = agenteNoEstado[memberName]?.status || ''
        const agentReason = agenteNoEstado[memberName]?.reason || ''
        this.color(agentReason)
        this.colorName(agentReason)
        this.colorClass(agentReason)
        if (agentStatus === 'Online' || agentStatus === 'Ocupado') {
          if (agentReason !== agent.reason) {
            agent.startTime = Date.now()
          }
          const dif = Date.now() - agent.startTime
          this.$set(agent, 'formattedElapsedTime', dif / 1000)
          this.$set(agent, 'reason', agentReason)
        } else {
          this.reiniciarCronometroParaAgente(memberName)
        }
      })

      this.$forceUpdate()
      setTimeout(this.actualizar, 1000)
    }

  },
  created () {
    this.agents = {}

    this.iniciar()
  },
  beforeDestroy () {
    clearTimeout(this.idTimeout)
  }
}
</script>

<style>
.table th, .table td {
  vertical-align: sub;
}
button.btn.btn-sm.btn-outline-danger.round {
  border-radius: 20px;
}
</style>
