<template>
  <b-modal v-model="showModal" title="Agentes em Pausa">
    <div class="card-content mt-1">
      <div class="table-responsive">
        <table id="tableagentTitle" class="table table-hover table-xl mb-0">
          <thead>
            <tr>
              <th class="border-top-0 text-center">Nome</th>
              <th class="border-top-0 text-center">Motivo</th>
              <th class="border-top-0 text-center">Qtd</th>
              <th class="border-top-0 text-center">Tempo</th>
            </tr>
          </thead>
          <tbody id="filaAgents" v-if="Object.keys(PausedAgents).length > 0">
            <tr
              v-for="(status, extension) in PausedAgents"
              :key="extension"
              :class="{
                'time-exceeded': status.isTimeLimitExceeded,
                'half-time': status.isHalfTime,
              }"
            >
              <td class="text-truncate text-center" id="posicao">
                <strong>{{ status.name }}</strong>
              </td>
              <td class="text-truncate text-center p-1">
                <strong>{{ status.reason }}</strong>
              </td>
              <td class="text-truncate text-center p-1">
                <strong> {{ status.pauseCount }}</strong>
              </td>
              <td class="text-center">
                <div class="d-flex-center">
                  <button type="button" class="btn btn-sm btn-outline-danger round">
                    <strong>{{ formatoSegundos(status.formattedElapsedTime) }}</strong>
                  </button>
                  <span
                    v-if="status.isTimeLimitExceeded"
                    style="display: block; font-size: 10px; color: red"
                    >Tempo excedido!</span
                  >
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </b-modal>
</template>

<script>
import { state } from "@/socket";
import axios from "axios";

export default {
  name: "EventsSocket",
  data() {
    return {
      showModal: false,
      agents: {},
      idTimeout: null,
      pausedBreaks: [],
    };
  },
  mounted() {
    this.getSavedBreaks();
  },
  computed: {
    PausedAgents() {
      const allAgents = Object.fromEntries(state.QueueMemberAgent) || {};
      return Object.entries(allAgents)
        .filter(([_, agent]) => agent.status === "Paused")
        .reduce((obj, [key, value]) => {
          if (!this.agents[key]) {
            this.$set(this.agents, key, { ...value });
          }
          obj[key] = this.agents[key];
          return obj;
        }, {});
    },
  },
  methods: {
    zeroIzq(n) {
      return n.toString().replace(/^(\d)$/, "0$1");
    },

    async getSavedBreaks() {
      try {
        const response = await axios.get(`${location.origin}/breaks`);
        this.pausedBreaks = response.data.filter(
          (breakItem) => breakItem.reason !== "Hold"
        );
      } catch (error) {
        console.error("Erro ao carregar dados:", error);
        return [];
      }
    },

    formatoSegundos(s) {
      return (
        this.zeroIzq(Math.floor(s / 3600)) +
        ":" +
        this.zeroIzq(Math.floor((s % 3600) / 60)) +
        ":" +
        this.zeroIzq(Math.floor((s % 3600) % 60))
      );
    },
    reiniciarCronometroParaAgente(memberName) {
      this.$delete(this.agents, memberName);
    },

    convertEditedTimeToSeconds(data) {
      return data.map((item) => {
        const [hours, minutes] = item.editedTime.split(":");

        return {
          ...item,
          editedTimeInSeconds: parseInt(hours, 10) * 3600 + parseInt(minutes, 10) * 60,
        };
      });
    },

    actualizar() {
      const breaks = this.pausedBreaks;
      Object.keys(this.agents).forEach((memberName) => {
        const agent = this.agents[memberName];
        const dif = Date.now() - agent.startTime;
        this.$set(agent, "formattedElapsedTime", dif / 1000);

        const agenteNoEstado = Object.fromEntries(state.QueueMemberAgent) || {};
        const agentStatus = agenteNoEstado[memberName]?.status || "";

        if (agentStatus === "Paused") {
          const agentReason = agenteNoEstado[memberName]?.reason || "";
          const matchingBreak = breaks.find((item) =>
            item.reason.includes(agentReason.trim())
          );
          if (matchingBreak) {
            if (agent.formattedElapsedTime > matchingBreak.time) {
              agent.isTimeLimitExceeded = true;
              agent.isHalfTime = false;
            } else if (agent.formattedElapsedTime > matchingBreak.time / 2) {
              agent.isHalfTime = true;
            }
          }
        } else {
          this.reiniciarCronometroParaAgente(memberName);
        }
      });

      const pausedAgents = Object.values(this.agents).filter(
        (agent) => agent.status === "Paused"
      );

      if (pausedAgents.length > 0) {
        this.$store.state.queueMemberAgent = {
          ...this.$store.state.queueMemberAgent,
          ...this.agents,
        };
        this.$emit("agentsData", this.$store.state.queueMemberAgent);
      }

      this.$forceUpdate();
      this.idTimeout = setTimeout(this.actualizar, 1000);
    },

    iniciar() {
      clearTimeout(this.idTimeout);
      Object.keys(this.agents).forEach((memberName) => {
        this.agents[memberName].startTime = Date.now();
      });
      this.actualizar();
    },
  },
  created() {
    this.agents = {};

    this.iniciar();
  },
  beforeDestroy() {
    clearTimeout(this.idTimeout);
  },
};
</script>

<style>
.table th,
.table td {
  vertical-align: sub;
}
button.btn.btn-sm.btn-outline-danger.round {
  border-radius: 20px;
}
tr.time-exceeded {
  color: #4e5155 !important;
  background-color: rgb(255 0 0 / 9%) !important;
}
tr.half-time {
  color: #4e5155 !important;
  background-color: yellow !important;
}
</style>
