<template>
  <div>
    <div class="d-flex">State &nbsp;
      <div :class="{ 'green-dot': connected, 'red-dot': !connected }"></div>
    </div>
  </div>
</template>

<script>
import { state } from '@/socket'

export default {
  name: 'ConnectionState',

  computed: {
    connected () {
      return state.connected
    }
  }
}
</script>

<style scoped>
.green-dot {
  width: 20px;
  height: 20px;
  background-color: green;
  border-radius: 50%;
  display: inline-block;
}

.red-dot {
  width: 20px;
  height: 20px;
  background-color: red;
  border-radius: 50%;
  display: inline-block;
}
</style>
