<script>
import { Pie, mixins } from 'vue-chartjs'
import _ from 'lodash'
export default {
  extends: Pie,
  mixins: [mixins.reactiveProp],
  mounted () {
    this.renderChart(
      {
        labels: ['Disponível', 'Pausa', 'Chamando', 'Ocupado'
        // 'Pós-Atend'
        ],
        datasets: [
          {
            data: [],
            backgroundColor: [
              '#02BC77',
              '#ffc107',
              '#6c757d',
              '#d9534f',
              '#007bff'
            ],
            hoverBackgroundColor: [
              '#02BC77',
              '#ffc107',
              '#6c757d',
              '#d9534f',
              '#007bff'
            ],
            borderColor: 'white'
          }
        ]
      },
      {
        responsive: true,
        maintainAspectRatio: true,
        legend: {
          display: true,
          position: 'bottom',
          labels: {
            filter: (legendItem, data) => {
              const label = legendItem.text
              const labelIndex = _.findIndex(
                data.labels,
                labelName => labelName === label
              )
              const qtd = data.datasets[0].data[labelIndex]
              legendItem.text = `${legendItem.text} : ${qtd}`
              return true
            }
          }
        }
      }
    )
  }
}
</script>
