<template>
  <div>
    <vue-speedometer
      style="margin-top:-20px"
      :maxSegmentLabels="3"
      :minValue="minValue"
      :maxValue="maxValue"
      :width="220"
      :ringWidth="45"
      :segments="3"
      :value="formattedValue"
      :customSegmentStops="segment"
      :segmentColors="!invert ? ['#d9534f', '#02BC77'] : ['#02BC77', '#d9534f']"
      :currentValueText="formattedValue.toString()"
    />
  </div>
</template>

<script>
import VueSpeedometer from 'vue-speedometer'

export default {
  name: 'speedometer',
  props: ['minValue', 'maxValue', 'segment', 'invert', 'value'],
  components: {
    VueSpeedometer
  },
  computed: {
    formattedValue () {
      return parseFloat(this.value)
    }
  },

  watch: {
    formattedValue: {
      handler (newVal, oldVal) {
        console.log('new', newVal)
        console.log('old', oldVal)
      },
      deep: true
    }

  }
}
</script>
