<template>
  <b-modal v-model="showModal" title="Agentes Offline">
    <div class="card-content mt-1">
      <div class="table-responsive">
        <table id="tableagentTitle" class="table table-hover table-xl mb-0">
          <thead>
            <tr>
              <th class="border-top-0 text-center">
                Nome
              </th>
              <th class="border-top-0 text-center">Status</th>
              <th class="border-top-0 text-center">Data</th>
            </tr>
          </thead>
          <tbody id="filaAgents" v-if="Object.keys(offlineAgents).length > 0">
            <tr v-for="(status, extension) in offlineAgents" :key="extension">
              <td class="text-truncate text-center" id="posicao">
                <strong>{{ status.name }}  <span>{{ status.pauseCount }}</span> </strong>
              </td>
              <td class="text-truncate text-center p-1">
                <strong> {{ status.status }}</strong>
              </td>
              <td class="text-center">
                <button type="button" class="btn btn-sm round">
                  <strong>{{ formatDate(status.startTime) }}</strong>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </b-modal>

</template>

<script>
import { state } from '@/socket'
import moment from 'moment'

export default {
  name: 'Offline',
  data () {
    return {
      showModal: false
    }
  },
  computed: {
    offlineAgents () {
      const allAgents = Object.fromEntries(state.QueueMemberAgent)
      return Object.entries(allAgents)
        .filter(([_, agent]) => agent.status === 'Offline')
        .reduce((obj, [key, value]) => {
          obj[key] = value
          return obj
        }, {})
    }
  },
  methods: {
    fecharModal () {
      this.showModal = false
    },
    formatDate (second) {
      return moment(second).format('DD/MM/YYYY HH:mm:ss')
    },
    formatarHoraMinutoSegundo (timestamp = Date.now()) {
      const data = new Date(timestamp)

      const horas = data.getHours()
      const minutos = data.getMinutes()
      const segundos = data.getSeconds()

      const horaFormatada = `${horas.toString().padStart(2, '0')}:${minutos.toString().padStart(2, '0')}:${segundos.toString().padStart(2, '0')}`

      return horaFormatada
    }

  }
}
</script>

<style>
.table th, .table td {
  vertical-align: sub;
}
button.btn.btn-sm.btn-outline-danger.round {
  border-radius: 20px;
}
</style>
