<template>
  <b-modal v-model="showModal" title="Agentes em Pausa Excedida" size="lg">
    <div class="card-content mt-1">
      <div class="table-responsive">
        <table id="tableagentTitle" class="table table-hover table-xl mb-0">
          <thead>
            <tr>
              <th class="border-top-0 text-center">
                Nome
              </th>
              <th class="border-top-0 text-center">
                Ramal
              </th>
              <th class="border-top-0 text-center">
                Início
              </th>
              <th class="border-top-0 text-center">
                Duração
              </th>
              <th class="border-top-0 text-center">
                fim
              </th>
              <th class="border-top-0 text-center">
                Motivo
              </th>
              <th class="border-top-0 text-center">
                Data
              </th>
            </tr>
          </thead>
          <tbody id="filaAgents" v-if="Object.keys(PausedAgents).length > 0">
            <tr v-for="( item, key) in PausedAgents" :key="key">
              <td class="text-truncate text-center">
                <strong>{{ item.name }}</strong>
              </td>
              <td class="text-truncate text-center">
                <strong>{{ item.interface }}</strong>
              </td>
              <td class="text-truncate text-center">
                <strong>{{ format(item.startTime) }}</strong>
              </td>
              <td class="text-truncate text-center">
                <strong>{{ formatTime(item.duration) }}</strong>
              </td>
              <td class="text-truncate text-center">
                <strong>{{ format(item.endTime) }}</strong>
              </td>
              <td class="text-truncate text-center">
                <strong>{{ item.reason }}</strong>
              </td>
              <td class="text-truncate text-center">
                <strong>{{ formatDate(item.endTime) }}</strong>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="text-right mt-2">
      <b-button @click="clearTable" v-if="Object.keys(PausedAgents).length > 0" variant="danger">Limpar</b-button>
    </div>
  </b-modal>

</template>

<script>
import { state, socket } from '@/socket'
import moment from 'moment'

export default {
  name: 'Paused',
  props: {
    showModal: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      agents: {}
    }
  },
  computed: {
    PausedAgents () {
      const allAgents = Object.fromEntries(state.ExceededLimit) || {}
      return Object.entries(allAgents)
        .reduce((obj, [key, value]) => {
          obj[key] = value
          return obj
        }, {})
    }

  },

  methods: {
    formatTime (seconds) {
      return moment.utc(seconds * 1000).format('HH:mm:ss')
    },
    format (second) {
      return moment(second).format('HH:mm:ss')
    },
    formatDate (second) {
      return moment(second).format('DD/MM/YYYY')
    },
    closeModal () {
      this.$emit('update:showModal', false)
    },
    clearTable () {
      socket.emit('clear', 'send clear')
    }
  }

}
</script>
