<template>
  <div>
    <div class="row">
      <div class="col">
        <b-form-group>
          Por página: &nbsp;
          <b-select
            size="sm"
            :options="perPageOptions"
            v-model="perPage"
            class="d-inline-block w-auto"
          />
        </b-form-group>
      </div>
      <div class="row">
        <div class="col">
          <b-button size="sm" variant="success" @click="btnStatus = 'Online'"
            >Online</b-button
          >
          <b-button size="sm" variant="warning" @click="btnStatus = 'Paused'"
            >Pausa</b-button
          >
          <b-button size="sm" variant="danger" @click="btnStatus = 'Offline'"
            >Offline</b-button
          >
          <b-button size="sm" variant="primary" @click="btnStatus = ''">Todos</b-button>
        </div>
      </div>
      <div class="col">
        <b-form-group>
          <b-input
            size="sm"
            placeholder="Procurar..."
            class="d-inline-block w-auto float-sm-right"
            v-model="search"
            @change="filter"
          />
        </b-form-group>
      </div>
    </div>
    <div class="table-responsive">
      <b-table
        :items="filter"
        :sort-icon-left="true"
        :fields="tableFields"
        :sticky-header="false"
        :striped="true"
        :hover="true"
        :bordered="true"
        :borderless="true"
        :outlined="true"
        :small="true"
        :fixed="true"
        :selectable="true"
        :current-page="currentPage"
        :per-page="perPage"
      >
        <template v-slot:cell(TTA)="{ item }">
          <span v-b-tooltip.hover="'Tempo Total de Atendimento'"
            ><span class="ion ion-ios-call rotate-270"></span> {{ item.TTA }}</span
          >
        </template>
        <template v-slot:cell(TMA)="{ item }">
          <span v-b-tooltip.hover="'Tempo Médio de Atendimento'"
            ><span class="ion ion-ios-call rotate-270"></span> {{ item.TMA }}</span
          >
        </template>
        <template v-slot:cell(atendimentos)="{ item }">
          <span
            ><span class="ion ion-ios-call rotate-270"></span>
            {{ item.Atendimentos }}</span
          >
        </template>
        <template v-slot:cell(nome)="{ item }">
          <span><span :class="colorName(item.Status)"></span> {{ item.Nome }}</span>
        </template>
        <template v-slot:cell(status)="{ item }">
          <span :v-if="item.Status === 'Online'" :class="color(item.Status)">{{
            item.Status
          }}</span>
        </template>
      </b-table>
    </div>
    <div class="row">
      <div class="col-sm text-sm-left text-center mb-3 mb-sm-0">
        <span class="text-muted" v-if="totalPages"
          >Página {{ currentPage }} de {{ totalPages }}</span
        >
      </div>
      <div class="col-sm">
        <b-pagination
          class="justify-content-center justify-content-sm-end"
          v-if="totalItems"
          v-model="currentPage"
          :total-rows="totalItems"
          :per-page="perPage"
          size="sm"
        />
      </div>
    </div>
    <export
      :table="tableData"
      :header="tableHeader"
      :filename="tableFilename"
      :footer="tablefooter"
      :legend="legend"
    />
  </div>
</template>
<script>
import Export from "@/components/Export";
import moment from "moment";
import { state } from "@/socket";
import axios from "axios";
export default {
  components: {
    Export,
  },
  data() {
    return {
      legend: "",
      tablefooter: "",
      tableData: [],
      allAgents: [],
      table: [],
      breaks: [],
      btnStatus: "",
      idTimeout: null,
      tableHeader: `dashboard_${moment().format("YYYY-MM-DD_HH-mm-ss")}`,
      tableFilename: `dashboard_${moment().format("YYYY-MM-DD_HH-mm-ss")}`,
      currentPage: 1,
      perPage: 10,
      perPageOptions: [10, 15, 20, 25, 30],
      search: "",
      totalTable: [],
    };
  },
  computed: {
    eventReport() {
      const allAgents = Object.fromEntries(state.QueueMemberAgent);
      let agents;

      if (this.btnStatus === "") {
        agents = allAgents;
      } else {
        agents = Object.entries(allAgents)
          .filter(
            ([_, agent]) =>
              agent.status !== null &&
              agent.status !== undefined &&
              this.btnStatus.includes(agent.status)
          )
          .reduce((obj, [key, value]) => {
            if (!this.allAgents[key]) {
              this.$set(this.allAgents, key, { ...value });
            }
            obj[key] = this.allAgents[key];
            return obj;
          }, {});
      }

      this.report(agents);
      return agents;
    },

    tableFields() {
      if (!this.tableData[0]) return [];
      const keys = Object.keys(this.tableData[0]);
      const filteredKeys = keys.filter((key) => key !== "_rowVariant");
      return filteredKeys.map((key) => ({
        key,
        sortable: this.isSortable,
      }));
    },
    totalItems() {
      return this.tableData.length;
    },
    totalPages() {
      return (
        Math.floor(Math.ceil(this.totalItems / this.perPage)) || (this.totalItems ? 1 : 0)
      );
    },
    filter() {
      const lista = this.tableData;
      if (this.search) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.currentPage = 1;
        return lista.filter((res) => {
          res = Object.values(res);
          for (let k = 0; k < res.length; k++) {
            if ((res[k] + "").toLowerCase().indexOf(this.search.toLowerCase()) >= 0) {
              return true;
            }
          }
          return false;
        });
      }
      return lista;
    },
  },
  mounted() {
    this.getSavedBreaks();
  },
  created() {
    this.allAgents = {};

    this.iniciar();
  },
  methods: {
    async getSavedBreaks() {
      try {
        const response = await axios.get(`${location.origin}/breaks`);
        this.pausedBreaks = response.data.filter(
          (breakItem) => breakItem.reason !== "Hold"
        );
      } catch (error) {
        console.error("Erro ao carregar dados:", error);
        return [];
      }
    },
    report(allAgents) {
      this.allAgents = Object.entries(allAgents);

      const tableTemp = [];
      this.allAgents.forEach(([key, agentTable]) => {
        if (agentTable.status !== undefined && agentTable.reason !== undefined) {
          const elapsedTime = Math.floor((Date.now() - agentTable.startTime) / 1000);
          let formattedMediaTMA = "00:00:00";
          let formattedTA = "00:00:00";
          if (
            agentTable.tempoAtendimento !== undefined &&
            agentTable.atendidas !== undefined &&
            agentTable.atendidas !== 0
          ) {
            formattedMediaTMA = moment()
              .startOf("day")
              .seconds(agentTable.tempoAtendimento / agentTable.atendidas)
              .format("HH:mm:ss");
            formattedTA = moment()
              .startOf("day")
              .seconds(agentTable.tempoAtendimento)
              .format("HH:mm:ss");
          }

          const loginTime = moment(agentTable.ultimo_login).format("DD/MM/YYYY HH:mm:ss");

          const tempoStatus =
            agentTable.status === "Offline"
              ? "00:00:00"
              : this.formatoSegundos(elapsedTime);
          const cellVariants = {
            isTimeLimitExceeded: agentTable.isTimeLimitExceeded ? "danger" : "",
            isHalfTime: agentTable.isHalfTime ? "warning" : "",
          };
          const user = {
            Ramal: agentTable.extension,
            Nome: agentTable.name,
            Status: agentTable.reason,
            Abandono: agentTable.abandonadas ?? 0,
            Atendimentos: agentTable.atendidas ?? 0,
            "Tempo Status": tempoStatus,
            TTA: formattedTA ?? "00:00:00",
            TMA: formattedMediaTMA ?? "00:00:00",
            Telefone: agentTable.ultimo_telefone ?? "---",
            Login: loginTime,
            _rowVariant: cellVariants.isTimeLimitExceeded || cellVariants.isHalfTime,
          };
          tableTemp.push(user);
        }
      });
      this.tableData = tableTemp;
    },
    formatoSegundos(s) {
      const hours = Math.floor(s / 3600);
      const minutes = Math.floor((s % 3600) / 60);
      const seconds = Math.floor(s % 60);
      return `${this.zeroIzq(hours)}:${this.zeroIzq(minutes)}:${this.zeroIzq(seconds)}`;
    },
    zeroIzq(n) {
      return n.toString().padStart(2, "0");
    },
    iniciar() {
      this.actualizar();
    },
    reiniciarCronometroParaAgente(memberName) {
      this.$delete(this.allAgents, memberName);
    },
    actualizar() {
      const breaks = this.pausedBreaks;

      Object.entries(this.allAgents).forEach(([memberKey, [memberName, agent]]) => {
        const agentStatus = agent.status;
        const agentReason = agent.reason;
        const dif = Date.now() - agent.startTime;
        if (agentReason === undefined || agentReason === null) {
          return;
        }
        this.$set(agent, "formattedElapsedTime", dif / 1000);
        if (agentStatus === "Paused") {
          const matchingBreak = breaks.find((item) =>
            item.reason.includes(agentReason.trim())
          );
          if (matchingBreak) {
            if (agent.formattedElapsedTime > matchingBreak.time) {
              agent.isTimeLimitExceeded = true;
              agent.isHalfTime = false;
            } else if (agent.formattedElapsedTime > matchingBreak.time / 2) {
              agent.isHalfTime = true;
            }
          }
        }
        this.reiniciarCronometroParaAgente(memberName);
      });

      this.$forceUpdate();
      setTimeout(this.actualizar, 1000);
    },

    color(status) {
      if (status === "Disponível") {
        return "text-success";
      } else if (status === "Ocupado") {
        return "text-danger";
      } else if (status === "Chamando") {
        return "text-muted";
      } else if (status === "Offline") {
        return "text-danger";
      } else return "text-warning";
    },
    colorName(status) {
      if (status === "Disponível") {
        return "ion ion-md-person text-success";
      } else if (status === "Ocupado") {
        return "ion ion-md-person text-danger";
      } else if (status === "Chamando") {
        return "ion ion-md-person text-muted";
      } else if (status === "Offline") {
        return "ion ion-md-person text-danger";
      } else return "ion ion-md-person text-warning";
    },
  },

  beforeDestroy() {
    clearTimeout(this.idTimeout);
  },
  watch: {
    eventReport: {
      handler(newVal, oldVal) {},
      deep: true,
    },
  },
};
</script>
