<script>
import { Doughnut, mixins } from 'vue-chartjs'
import _ from 'lodash'

export default {
  extends: Doughnut,
  mixins: [mixins.reactiveProp],
  mounted () {
    this.renderChart(
      {
        labels: ['Atendidas', 'Abandonadas'],
        datasets: [
          {
            data: [0, 0, 0, 0],
            backgroundColor: ['#02BC77', '#d9534f'],
            hoverBackgroundColor: ['#02BC77', '#d9534f'],
            borderColor: 'white'
          }
        ]
      },
      {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: true,
          position: 'bottom',
          labels: {
            filter: (legendItem, data) => {
              const label = legendItem.text
              const labelIndex = _.findIndex(
                data.labels,
                labelName => labelName === label
              )
              const qtd = data.datasets[0].data[labelIndex]
              legendItem.text = `${legendItem.text} : ${qtd}`
              return true
            }
          },
          onClick: this.handle
        }
      }
    )
  },
  methods: {
    handle (point, event) {
      this.$emit('showModal', event.text)
    }
  }
}
</script>
